// modalService.js
import mitt from 'mitt';

// Âª∫Á´ã‰∏ÄÂÄãÂÖ®Â±Ä event bus
export const modalBus = mitt();

modalBus.once = function (ev, cb) {
  const handler = () => {
    cb();
    modalBus.off(ev, handler);
  };
  modalBus.on(ev, handler);
  return modalBus;
};

// Â∞àÊ°àÂ∞àÁî®ÁöÑ ModalService
export const ModalService = {
  showLocked(message = 'ËôïÁêÜ‰∏≠ÔºåË´ãÁ®çÂÄô‚Ä¶') {
    modalBus.emit('showLocked', message);
    return modalBus;
  },

  showInfo({ message = '', delay = 3000, callback = null } = {}) {
    modalBus.emit('showInfo', { message, delay });
    if (typeof callback === 'function') {
      modalBus.once('closed', callback); // Á¢∫‰øùÂè™Âü∑Ë°å‰∏ÄÊ¨°
    }
    return modalBus;
  },

  showYesNo(message = 'ÂÜçÊ¨°Á¢∫Ë™ç‚Ä¶', yes, no) {
    modalBus.emit('showYesNo', { message, yes, no });
    return modalBus;
  },

  close() {
    modalBus.emit('closeModal');
    return modalBus;
  },
};

