<template>
  <div class="menu-wrapper" :style="menuStyle">
    <video v-if="isVideoBg"
      class="bg-video"
      autoplay
      loop
      muted
      playsinline
    >
      <source :src="bgUrl" type="video/mp4" />
    </video>
    <div class="menu-content">
      <!-- Â∑¶ÁøªÈ†Å -->
      <button class="nav-btn left" @pointerdown.prevent="goPrev">
        <span class="arrow">‚Üê</span>
      </button>

      <!-- ÂïÜÂìÅÂçÄÂ°ä -->
      <swiper
        :modules="modules"
        :slides-per-view="1"
        :space-between="30"
        pagination
        class="menu-swiper"
        @swiper="onSwiper"
      >
        <swiper-slide
          v-for="(page, pageIndex) in pagedProducts"
          :key="pageIndex"
        >
          <div class="grid-container" :class="`rows-${Math.ceil(page.length / 4)}`">
            <div
              v-for="item in page"
              :key="item.id"
              class="product-grid"
              @pointerdown.prevent="!item.soldout && ordered(item)"
            >
              <div class="product-card">
                <img :src="item.img" :alt="item.title" class="product-img" />
                <div v-if="item.soldout" class="soldout-overlay">
                  <span class="soldout-text">ÂîÆÂÆå</span>
                </div>
              </div>
              <div class="product-title">{{ item.title }}</div>
              <div class="product-price">{{ item.price }} ÂÖÉ</div>
            </div>
          </div>
        </swiper-slide>
      </swiper>

      <!-- Âè≥ÁøªÈ†Å -->
      <button class="nav-btn right" @pointerdown.prevent="goNext">
        <span class="arrow">‚Üí</span>
      </button>
    </div>

    <!-- Modal with animation -->
    <transition name="fade" mode="out-in">
      <div v-if="selected" class="modal-overlay">
        <transition name="zoom" mode="out-in">
          <div class="modal" v-if="selected">
            <button class="modal-close" @pointerdown.prevent="closeModal">‚úï</button>
            <img :src="selected.img" class="modal-img" />
            <h3 class="modal-title">{{ selected.title }}</h3>
            <p class="modal-price">ÂîÆÂÉπÔºö{{ selected.price }} ÂÖÉ</p>
            <p v-if="selected.desc" class="modal-desc">{{ selected.desc }}</p>
            <p v-show="!pmReady" class="pay-list">ÈñãÂïüÊîØ‰ªòÈÅ∏ÂñÆÔºåË´ãÁ®çÂæå‚Ä¶</p>

            <!-- ÊîØ‰ªòÊñπÂºèË™™Êòé -->
            <div class="pay-list" v-show="!cashSelected && pmReady">
              <p>Ë´ãÈÅ∏Áî®‰ª•‰∏ãÊîØ‰ªòÊñπÂºèÔºö</p>
              <ol>
                <li
                  v-for="(opt, method) in payOptions"
                  :key="method"
                  class="pay-item"
                  @pointerdown.prevent="selectPay(method)"
                >
                  <img :src="payIcon(method)" class="pay-icon" />
                  <span>{{payText(method)}}</span>
                </li>
              </ol>
            </div>
            <div v-show="cashSelected" class="cash-panel">
              <div class="cash-info">
                <span class="label">Ë´ãÊäïÂÖ•ÁèæÈáëÔºåÂ∑≤ÊäïÂÖ•ÈáëÈ°çÔºö</span>
                <span class="amount">{{ insertedAmount }} ÂÖÉ</span>
              </div>
              <p class="cash-warning">ÁÇ∫ÈÅøÂÖçÂêÉÈå¢ÔºåË´ã‰∏ÄÂÄã‰∏ÄÂÄãÊäïÂÖ•ÔºÅ</p>
            </div>
          </div>
        </transition>
      </div>
    </transition>
  </div>
</template>

<script>
import axios from 'axios'
import { Swiper, SwiperSlide } from 'swiper/vue'
import { Navigation, Pagination } from 'swiper/modules'
import 'swiper/css'
import 'swiper/css/navigation'
import 'swiper/css/pagination'

export default {
  name: 'GridMenu',
  components: { Swiper, SwiperSlide },
  props: {
    insertedAmount: Number,
    pmReady: Boolean,
  },
  data() {
    return {
      payOptions: null,
      products: [],
      swiperInstance: null,
      selected: null,
      cashSelected: false,
      bgUrl: null,
    }
  },
  computed: {
    isVideoBg() {
      return this.bgUrl && /\.(mp4|webm)$/i.test(this.bgUrl);
    },
    menuStyle() {
      return {
        backgroundImage: this.bgUrl ? `url(${this.bgUrl})` : 'none',
        backgroundSize: 'cover',
        backgroundPosition: 'center',
      }
    },
    pagedProducts() {
      const size = 16
      const pages = []
      for (let i = 0; i < this.products.length; i += size) {
        pages.push(this.products.slice(i, i + size))
      }
      return pages
    },
    modules() {
      return [Navigation, Pagination]
    },
  },
  mounted() {
    this.loadProducts();
    this.loadBackground();
  },
  methods: {
    async loadBackground() {
      const params = new URLSearchParams(window.location.search);
      const bgFromQuery = params.get('bg');
      if (bgFromQuery) {
        this.bgUrl = bgFromQuery;
        return;
      }
      const localBg = `${window.location.origin}/media/gui/bg.png`;
      const fallback = require('@/assets/bg.png');

      const found = await this.checkImage(localBg);
      this.bgUrl = found ? localBg : fallback;
    },
    // Âà§Êñ∑ÂúñÁâáÊòØÂê¶Â≠òÂú®
    checkImage(src) {
      return new Promise(resolve => {
        const img = new Image();
        img.onload = () => resolve(true);
        img.onerror = () => resolve(false);
        img.src = src + `?t=${Date.now()}`;
        // Âä† timestamp ÈÅøÂÖç cache Â∞éËá¥Âà§Êñ∑ÈåØË™§
      });
    },
    async loadProducts() {
      try {
        // Ëß£Êûê URL ÂèÉÊï∏
        const urlParams = new URLSearchParams(window.location.search);
        const restParam = urlParams.get('rest');

        // Ê±∫ÂÆöË¶ÅÂëºÂè´ÁöÑ API URL
        const restURL = restParam
        ? decodeURIComponent(restParam)
        : `${window.location.origin}/app/rest/stock.cgi`;
        const res = await axios.get(`${restURL}/app/rest/stock.cgi`)
        this.products = res.data
      } catch (err) {
        console.error('ËºâÂÖ•ÂïÜÂìÅÂ§±Êïó', err)
      }
    },
    selectPay(pm) {
      if(pm === 'cash'){
        this.cashSelected=true;
      }
      this.$emit('paymentSelected',pm);
    },
    onSwiper(swiper) {
      this.swiperInstance = swiper
    },
    goPrev() {
      this.swiperInstance?.slidePrev()
    },
    goNext() {
      this.swiperInstance?.slideNext()
    },
    ordered(item){
      this.openModal(item);
      this.$emit('ordered',item);
    },
    openModal(item) {
      this.selected = item
    },
    closeModal() {
      this.selected = null
      this.cashSelected = false
      this.$emit('closed');
    },
    payText(method){
      switch (method) {
        case "cash": return "ÁèæÈáë - ÈªûÊ≠§ÈÄ≤Ë°åÁèæÈáëÊîØ‰ªò";
        case "creditcard": return "‰ø°Áî®Âç° - ÈªûÊ≠§ÈÄ≤Ë°å‰ø°Áî®Âç°ÊîØ‰ªò";
        case "easycard": return "ÈõªÂ≠êÁ•®Âç∑ - ÈªûÊ≠§‰ΩøÁî®ÊÇ†ÈÅäÂç°/‰∏ÄÂç°ÈÄö";
        case "isc_linepay": return "ÈõªÂ≠êÊîØ‰ªò - ÈªûÊ≠§ÈÄ≤Ë°åÈõªÂ≠êÊîØ‰ªòÔºåÂ∞áÊîØ‰ªò‰∫åÁ∂≠Á¢ºÂ∞çÊ∫ñÊ¢ùÁ¢ºÊéÉÊèèÂô®";
        default: return method;
      }
    },
    payIcon(method) {
      switch (method) {
        case "cash": return require('@/assets/cash.png');
        case "creditcard": return require('@/assets/card.png');
        case "easycard": return require('@/assets/card.png');
        case "isc_linepay": return require('@/assets/qrcode.png');
        default: return require('@/assets/qrcode.png');
      }
    },
  },
}
</script>

