<template>
  <div>
    <!-- ÈÅÆÁΩ© -->
    <transition name="fade">
      <div v-if="visible" class="modal-overlay" @mousedown="handleOverlayClick"></div>
    </transition>

    <!-- Modal -->
    <transition name="zoom">
      <div v-if="visible" class="modal">
        <div v-if="mode !== 'yesno'" class="modal-content">{{ message }}</div>
        <div v-if="mode === 'yesno'" class="modal-content">{{ message }}</div>

        <!-- ÊåâÈàïÂçÄÂüü -->
        <button v-if="mode === 'info'" class="modal-btn" @mousedown="close">ÈóúÈñâ</button>
        <div v-if="mode === 'yesno'" class="btn-group">
          <button class="modal-btn yes" @mousedown="yes">ÊòØ</button>
          <button class="modal-btn no" @mousedown="no">Âê¶</button>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import { modalBus } from '../services/modalService';

export default {
  name: 'MsgModal',
  data() {
    return {
      timer: null,
      visible: false,
      message: '',
      mode: 'info', // 'locked', 'info', 'yesno'
      delay: 3000,
      yesCallback: null,
      noCallback: null,
    };
  },
  methods: {
    showInfo(message = '', delay = 3000) {
      this.mode = 'info';
      this.message = message;
      this.visible = true;
      this.delay = delay;
      this._clearTimer();
      this.timer = setTimeout(() => {
        this.close();
      }, delay);
    },
    showLocked(message = '') {
      this.mode = 'locked';
      this._clearTimer();
      this.message = message;
      this.visible = true;
    },
    showYesNo({ message, yes, no }) {
      this.mode = 'yesno';
      this.message = message;
      this.yesCallback = yes;
      this.noCallback = no;
      this.visible = true;
    },
    yes() {
      if (typeof this.yesCallback === 'function') this.yesCallback();
      this.visible = false;
    },
    no() {
      if (typeof this.noCallback === 'function') this.noCallback();
      this.visible = false;
    },
    close() {
      this._clearTimer();
      this.visible = false;
      modalBus.emit('closed');
    },
    handleOverlayClick() {
      if (this.mode === 'info') {
        this.close();
      }
    },
    _clearTimer() {
      if (this.timer) {
        clearTimeout(this.timer);
        this.timer = null;
      }
    },
  },
  created() {
    modalBus.on('showLocked', this.showLocked);
    modalBus.on('showInfo', (info) => {
      this.showInfo(info.message, info.delay);
    });
    modalBus.on('showYesNo', (info) => {
      this.showYesNo(info);
    });
    modalBus.on('closeModal', this.close);
  },
};
</script>

