<template>
  <div v-if="items.length" class="gui-menu">
    <button
      v-for="func in items"
      :key="func.id"
      class="gui-menu-btn"
      @pointerdown.prevent="onPress(func)"
    >
      <span v-if="func.icon" class="gui-menu-icon">{{ func.icon }}</span>
      <span class="gui-menu-label">{{ func.label }}</span>
    </button>
  </div>
</template>

<script>
import ebusService from '../services/ebusService.js'

export default {
  name: 'GuiMenu',
  props: {
    /**
     * gui_menu ÂäüËÉΩÊåâÈàïÂàóË°®
     * ÊØèÂÄãÈ†ÖÁõÆÁµêÊßã:
     * {
     *   id: String,        // ÂîØ‰∏ÄË≠òÂà•Á¢ºÔºå‰æãÂ¶Ç 'exchange'
     *   label: String,     // È°ØÁ§∫ÊñáÂ≠óÔºå‰æãÂ¶Ç 'ÂÖåÈàî'
     *   icon: String,      // ÊåâÈàïÂúñÁ§∫ (emoji ÊàñÊñáÂ≠ó)
     *   event: String,     // (optional) ÈªûÊìäÊôÇÁôºÈÄÅÁöÑ ebus ‰∫ã‰ª∂ÂêçÁ®±
     *   arg: Object,       // (optional) ‰∫ã‰ª∂ÈôÑÂ∏∂ÂèÉÊï∏
     * }
     */
    items: {
      type: Array,
      default: () => []
    }
  },
  emits: ['press'],
  methods: {
    onPress(func) {
      // ÁôºÂá∫Áµ±‰∏ÄÁöÑ press ‰∫ã‰ª∂ÔºåÂ∏∂ÂõûÊï¥ÂÄã func Áâ©‰ª∂
      this.$emit('press', func)

      // Ëã•ÊúâÂÆöÁæ© ebus eventÔºåÂâáÈÄèÈÅé ebusService ÁôºÈÄÅ
      if (func.event) {
        ebusService.send(func.event, func.arg || {})
      }
    }
  }
}
</script>

