<template>
  <div class="h-screen w-screen flex relative">
    <div
      v-if="enableClickToAdmin"
      class="admin-hotspot"
      @pointerdown.prevent="adminTap"
    ></div>
    <div v-show="dev" class="ebus-status">
      <span>Ebus: {{ ebusConnected ? 'üü¢' : 'üî¥' }}</span>
    </div>
    <EbusListener class="hidden" v-model="ebusConnected"
      @ebus-event-payment-after-hint="paymentAfterHint"
      @ebus-event-payment-after-cancelled="paymentAfterCancelled"
      @ebus-event-payment-after-timeout="paymentAfterCancelled"
      @ebus-event-sys-after-hint="sysAfterHint"
      @ebus-event-sys-after-config-changed="sysAfterHint"
      @ebus-event-order-after-start="enterMENU"
      @ebus-event-sess-enter-SESSION="idle=false"
      @ebus-event-sess-enter-IDLE="enterIDLE"
      @ebus-event="messageHandler"
    />
    <MsgModal />
    <!-- Idle ÁãÄÊÖãÁáàÔºàÂõ∫ÂÆöÂ∑¶‰∏äËßíÔºâ -->
    <div v-if=true class="idle-status"
      title="Idle ÁãÄÊÖã"
  >
      <span class="text-3xl">
        {{ idle ? 'üí§' : 'üí°' }}
      </span>
      <span v-if="temperature !== null" class="text-lg mt-1">
        üå°Ô∏è {{ temperature }}¬∞C
      </span>
    </div>
    <div class="flex-1 flex items-center justify-center">
      <GridMenu ref='gridMenu'
        @closed='closed'
        @ordered='ordered'
        @paymentSelected='paymentSelected'
        @cashSelected='cashSelected'
        @creditcardSelected='creditcardSelected'
        v-model:inserted-amount="insertedAmount"
        v-model:pm-ready="pmReady"
      />
    </div>
    <GuiMenu :items="guiMenu" @press="onGuiMenuPress" />
  </div>
</template>

<script>
import axios from 'axios'
import GridMenu from './components/GridMenu.vue'
import GuiMenu from './components/GuiMenu.vue'
import MsgModal from './components/MsgModal.vue'
import init from './mixins/init'
import { ModalService } from './services/modalService'
import EbusListener from './components/EbusListener.vue'
import ebusService from './services/ebusService.js'
export default {
  components: {
    MsgModal,
    EbusListener,
    GridMenu,
    GuiMenu
  },
  mixins: [init],
  data() {
    return {
      ebusConnected: false,
      showModal: false,
      insertedAmount: 0,
      pmReady: false,
      idle: true,
      pendingOrder: null,
      temperature: null,
      guiMenu: [],
      // click-to-admin Ë®≠ÂÆö
      adminTapCount: 0,
      adminTapTimer: null,
      adminTapWindowMs: 2000, // Â§öÂø´ÁÆó„ÄåÂø´ÈÄüÈªûÊìä„Äç
      adminTapTarget: 6,
    }
  },
  computed: {
    enableClickToAdmin() {
      const params = new URLSearchParams(window.location.search);
      // ÊîØÊè¥ ?clicktoadmin Êàñ &clicktoadmin
      return params.has('clicktoadmin');
    },
  },
  methods: {
    adminTap() {
      // ÂÆâÂÖ®‰øùË≠∑ÔºöÊ≤íÈñãÂ∞±‰∏çÂÅö
      if (!this.enableClickToAdmin) return;
      // Á¨¨‰∏Ä‰∏ãÈñãÂßãË®àÊôÇ
      if (this.adminTapCount === 0) {
        if (this.adminTapTimer) clearTimeout(this.adminTapTimer);
        this.adminTapTimer = setTimeout(() => {
          this.adminTapCount = 0;
          this.adminTapTimer = null;
        }, this.adminTapWindowMs);
      }
      this.adminTapCount += 1;
      if (this.adminTapCount >= this.adminTapTarget) {
        // ÈÅîÊ®ôÔºöÈÄÅÂá∫‰∫ã‰ª∂
        ebusService.send('sys/sys_admin');
        // ÈáçÁΩÆ
        this.adminTapCount = 0;
        if (this.adminTapTimer) clearTimeout(this.adminTapTimer);
        this.adminTapTimer = null;
      }
    },
    loadCustomCss() {
      const params = new URLSearchParams(window.location.search);
      const cssFromQuery = params.get('css');
      const cssUrl = cssFromQuery
      ? cssFromQuery
      : `${window.location.origin}/media/gui/custom.css`;
      const link = document.createElement("link");
      link.rel = "stylesheet";
      link.href = cssUrl + `?t=${Date.now()}`; // Âä† timestamp Èò≤Ê≠¢ cache

      link.onload = () => {
        console.log("Custom CSS loaded:", cssUrl);
      };

      link.onerror = () => {
        console.warn("No custom.css found, using default styles.");
      };

      document.head.appendChild(link);
    },
    sysAfterHint(e) {
      if(e.e === "sys/after_config_changed" && e.arg?.act === "stock_changed"){
        this.$refs.gridMenu.loadProducts();
      }else if(e.e === "sys/after_hint"){
        if(e.arg?.stock_updated){
          this.$refs.gridMenu.loadProducts();
        }else if(e.arg?.tempreport){
          this.temperature = e.arg.tempreport.t;
        }
      }
    },
    async checkIdleStatus() {
      try {
        const urlParams = new URLSearchParams(window.location.search)
        const restParam = urlParams.get('rest')

        const restURL = restParam
          ? decodeURIComponent(restParam)
          : `${window.location.origin}/app/rest`

        const res = await axios.get(`${restURL}/app/rest/stat.cgi`)

        if (res.data && res.data.sess) {
          this.idle = res.data.sess.toUpperCase() === 'IDLE'
          console.log('[checkIdleStatus] sess =', res.data.sess, '‚Üí idle =', this.idle)
        } else {
          console.warn('[checkIdleStatus] stat.cgi Ê†ºÂºè‰∏çÊòéÔºö', res.data)
        }
      } catch (err) {
        console.error('ËÆÄÂèñ stat.cgi ÈåØË™§Ôºö', err)
      }
    },
    messageHandler(e){
      if(this.dev) console.log(e);
      if(e.arg?.info !== undefined){
        if(e.arg?.final){
          let cb=()=>{
            this.$refs.gridMenu.closeModal();
          };
          ModalService.showInfo({message:e.arg.info, callback: cb});
        }else{
          ModalService.showInfo({message:e.arg.info});
        }
      }else if(e.arg?.lock != undefined){
        ModalService.showLocked(e.arg.lock);
      }
    },
    enterMENU(e){
      if(e.to === 'MENU' && this.pendingOrder){
        ebusService.send('order/ordered', {"p_id":this.pendingOrder.id});
        this.pendingOrder=null;
      }
    },
    enterIDLE(){
      this.idle=true;
      this.$refs.gridMenu.closeModal();
    },
    ordered(item){
      if (this.idle) {
        ebusService.send('sess/session_begin');
        this.pendingOrder=item;
      } else {
        // Âê¶ÂâáÁ´ãÂç≥ÈÄÅÂá∫
        ebusService.send('order/ordered', {"p_id":item.id});
        this.pendingOrder=null;
      }
    },
    paymentAfterHint(e){
      if(e.arg?.payment_method){
        this.$refs.gridMenu.payOptions = e.arg.payment_method;
        this.pmReady = true;
        const methods = Object.keys(e.arg.payment_method);
        if (methods.length === 1 && methods[0] === 'cash') {
          console.log('[GridMenu] Ëá™ÂãïÈÅ∏ÊìáÁèæÈáëÊîØ‰ªò');
          this.$refs.gridMenu.selectPay('cash');
        }
      }
      if(e.arg?.paid){
        this.insertedAmount = e.arg.paid;
      }
    },
    paymentAfterCancelled(){
      ModalService.close();
      this.$refs.gridMenu.closeModal();
    },
    paymentSelected(pm){
      if(pm === 'creditcard'){
        ModalService.showLocked('Ë´ãÂ∞á‰ø°Áî®Âç°Èù†ËøëÂç°Ê©üÈÄ≤Ë°åÊÑüÊáâ');
      }else if(pm === 'isc_linepay'){
        ModalService.showLocked('Ë´ãÂá∫Á§∫LinepayÊîØ‰ªòÊ¢ùÁ¢ºÔºåÈù†ËøëÊéÉÁ¢ºÂô®');
      }
      ebusService.send("payment/input",{"method":pm,"payment_method":{[pm]:{}}});
    },
    closed(){
      this.insertedAmount = 0;
      this.pmReady = false;
      ebusService.send("payment/cancelled");
    },
    async loadGuiMenu() {
      const urlParams = new URLSearchParams(window.location.search)
      const restParam = urlParams.get('rest')
      const restURL = restParam
        ? decodeURIComponent(restParam)
        : `${window.location.origin}`
      try {
        const res = await axios.get(`${restURL}/app/rest/get_config.cgi`)
        if (res.data && Array.isArray(res.data.gui_menu)) {
          this.guiMenu = res.data.gui_menu
          console.log('[loadGuiMenu] loaded', this.guiMenu.length, 'items')
        } else {
          console.warn('[loadGuiMenu] gui_menu not found, using demo')
          this.guiMenu = this.demoGuiMenu()
        }
      } catch (err) {
        console.warn('[loadGuiMenu] get_config.cgi failed, using demo:', err.message)
        this.guiMenu = this.demoGuiMenu()
      }
    },
    onGuiMenuPress(func) {
      alert(JSON.stringify(func, null, 2))
    },
    demoGuiMenu() {
      return [
        // { id: 'exchange',  label: 'ÂÖåÈàî', icon: 'üíµ', event: 'order/ordered',  arg: {func:'exchange'} },
        // { id: 'cart',      label: 'Ë≥ºÁâ©Ëªä', icon: 'üõí', event: 'order/hint',     arg: {func:'cart'} },
        // { id: 'language',  label: 'Ë™ûË®Ä', icon: 'üåê' },
        // { id: 'member',    label: 'ÊúÉÂì°', icon: 'üë§', event: 'order/hint',     arg: {func:'member'} },
      ]
    }
  },
  async mounted() {
    this.loadCustomCss();
    this.$nextTick(() => {
      if (!this.ebusConnected && ebusService.connected) {
        this.ebusConnected = true;
      }
    });
    // Èò≤Ê≠¢ÂÖ©ÊåáÁ∏ÆÊîæ
    document.addEventListener('touchstart', function (e) {
      if (e.touches.length > 1) {
        e.preventDefault();
      }
    }, { passive: false });
    await this.checkIdleStatus();
    await this.loadGuiMenu();
  },
  beforeUnmount() {
    if (this.adminTapTimer) clearTimeout(this.adminTapTimer);
  },
}
</script>
<style>
html {
  width: 1080px;
  height: 1920px;
  margin: 0;
  padding: 0;
  overflow: hidden;
}
.ebus-status {
  position: fixed;
  top: 20px;
  right: 20px;
  background: rgba(0, 0, 0, 0.6);
  color: #fff;
  padding: 4px 8px;
  border-radius: 6px;
  font-size: 14px;
  z-index: 1000; /* Á¢∫‰øùÂú®ÊúÄ‰∏äÂ±§ */
}
.idle-status {
  position: fixed;
  top: 20px;
  left: 20px;
  padding: 4px 8px;
  border-radius: 6px;
  font-size: 14px;
  z-index: 1000; /* Á¢∫‰øùÂú®ÊúÄ‰∏äÂ±§ */
}
.admin-hotspot {
  position: fixed;
  top: 0;
  right: 0;
  width: 100px;   /* ÁÜ±ÂçÄÂ§ßÂ∞è‰Ω†ÂèØË™ø */
  height: 100px;
  z-index: 9999;
  background: transparent;
  /* Èò≤Ê≠¢Ë¢´ÈÅ∏ÂèñÊàñÈï∑ÊåâÈÅ∏ÂñÆ */
  /*border: 3px solid #22c55e;*/
  user-select: none;
  -webkit-user-select: none;
  -webkit-touch-callout: none;
}
</style>
